@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Red Hat Display', sans-serif;
    padding: 55px 5px 5px 5px;
}

div.Video {
    background: #ffe482;
    width: 90%;
    max-width: 800px;
    padding: 20px;
}

div.Video iframe {
    width: 100%;
    height: 400px;
}

div.Video h1 {
    margin-bottom: 8px;
}

div.header {
    position: fixed;
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #ccc;
    top: 0;
    left: 0;
    width: 100%;
}

div.header h1 {
    line-height: 50px;
    position: relative;
}

div.header h1 a {
    color: #000;
    text-decoration: none;
}

div.header h1 a::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    height: 80%;
    border-bottom: 2px solid #000;
    transition: width .3s;
}

div.header h1 a:hover::after {
    width: 100%;
}

div.Search {
    margin: 0 auto;
    margin-top: 13px;
    text-align: center;
}

div.Search input {
    width: 400px;
    height: 40px;
    font-size: 14pt;
}

div.Search button {
    width: 50px;
    height: 40px;
    font-size: 14pt;
}

p.description {
    white-space: pre-line;
}

i {
    vertical-align: text-bottom;
    margin-right: 5px;
}

p.count {
    margin-bottom: 10px;
}

hr {
    border: .5px solid #000;
    margin: 20px 0;
}

h3 {
    line-height: 35px;
}